import { API } from '../constants'

const services = {
  list: API + '/orders',
  byid: API + '/orders',
  delete: API + '/orders',
  register: API + '/orders',
}

const _token = localStorage.getItem('__token')

export async function getAllOrders(params?: any) {
  const requestOptions = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(
    params
      ? `${services.list}?start_date=${params.start || ''}&end_date=${params.end || ''}&status=${params.status || ''}`
      : services.list,
    requestOptions,
  ).then((response) => {
    res = response.json()
  })

  return res
}

export async function getOrderById(id: any) {
  const requestOptions = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(`${services.list}/${id}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export async function newOrder(body: any) {
  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
    body: JSON.stringify(body),
  }
  let res = null

  await fetch(services.register, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

